<template>
  <div class="hello">
      <div>{{this.person.name}} with id: {{this.person.id}}</div>
      <!-- <button @click="this.delete">Delete</button> -->
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'PersonComponent',
  props: {
    person: Object
  },
  methods:{
    delete(){
      axios.delete('http://localhost:3000/people/'+this.person.id).then(response => {
        console.log("Delete this person " + response.data.name);
      });
    }
  }
}
</script>

