<template>
  <div>
    <!-- <h1>My name is Kyle</h1>
    <input v-model="this.name">
    <button @click="updatePersonWithName">Click Me</button>
    <PersonComponent :person="person"></PersonComponent> -->

    <button @click="getAllPeople">Get All People</button>
    <PersonComponent v-for="p in people" :person="p" :key="p.name"></PersonComponent>
  </div>
</template>

<script>
import PersonComponent from "./components/PersonComponent.vue";
import axios from 'axios'

export default {
    name: "App",
    components: { PersonComponent },
    data(){
      return {
        name: '',
        person: {
          name: "kyle"
        },
        people: []
      }
    },
    methods: {
      updatePersonWithName(){
        this.person.name = this.name;
      },
      getAllPeople(){
        axios.get('http://localhost:7777/people').then(response => {
          let people = [];
          Object.keys(response.data).forEach(key => { people.push(response.data[key]); });
          this.people = people;
        }).catch(error => {
          console.log("Whatever " + error);
        });
      }
    }
}
</script>

